// import dependencies
import { Class } from 'es5-class-ployfill';
import 'intersection-observer';
import { mergeDeep } from '../util/mergeDeep';

// return UserList class


export class Iou {

  /**
   * construct Class
   */
  constructor(customOptions) {
    let settings = this.getSettings(customOptions)

    this.elementTrigger = settings.elementTrigger;
    this.functions = settings.functions;


    this.initialized = false;
  }

  getSettings(customOptions) {

    // set the defaults
    const defaults = {
      elementTrigger: '[data-iou-trigger]',
      functions: {
        addTargetClass(entry) {
          let entryTarget = entry.target;
          let targetString = entryTarget.getAttribute('data-iou-target');
          let classString = entryTarget.getAttribute('data-iou-addTargetClass');

          let re = /:\s|,\s/; // split on colon space or comma space
          let targetArray = targetString.split(re);
          let classArray = classString.split(re);

          targetArray.forEach(function (targetSel) {
            let currentEls = document.querySelectorAll(targetSel);

            currentEls.forEach(function (currentEl) {
              currentEl.classList.add(...classArray)
            })
          })
        },

        removeTargetClass(entry) {
          let entryTarget = entry.target;
          let targetString = entryTarget.getAttribute('data-iou-target');
          let classString = entryTarget.getAttribute('data-iou-addTargetClass');

          let re = /:\s|,\s/; // split on colon space or comma space
          let targetArray = targetString.split(re);
          let classArray = classString.split(re);

          targetArray.forEach(function (targetSel) {
            let currentEls = document.querySelectorAll(targetSel);

            currentEls.forEach(function (currentEl) {
              currentEl.classList.remove(...classArray)
            })
          })
        },
      }
    }

    // merge the customOptions with the default Options
    let settings = mergeDeep(defaults, customOptions);
    return settings;
  };


  /**
   * initialize plugin
   */

  init() {

    // render initial list of users
    this.bindLIstener();

    // set initialized to `true`
    this.initialized = true;

    console.log(this)
  }

  bindLIstener() {
    let elements = document.querySelectorAll(this.elementTrigger);

    let observer = new IntersectionObserver((entries) => {

      entries.forEach(entry => {
        if (entry.intersectionRatio > 0) {
          // element is in View
          let inAction = entry.target.getAttribute('data-iou-in-action');
          let inActionTop = entry.target.getAttribute('data-iou-in-action-top');
          let inActionBottom = entry.target.getAttribute('data-iou-in-action-bottom');
          let targetEl = entry.target.getAttribute('data-iou-target');
          let yPos = entry.boundingClientRect.top;

          // if no target is defined use the trigger element as target
          if (!targetEl) {
            targetEl = entry.target
          }

          // here we check if the Element comes in from top or bottom
          if (yPos < 0) {
            // yPos < 0 means the element comes in from top
            if (inActionTop) {
              // if there is a specific top function use it
              console.log('enter from top');
              this.functions[inActionTop](entry);
            } else {
              // otherwise use the general function
              console.log('enter in general');
              this.functions[inAction](entry);
            }
          } else if ((yPos > 0)) {
            // yPos > 0 means the element comes in from bottom
            if (inActionBottom) {
              // if there is a specific bottom function use it
              console.log('enter from bottom', entry);
              this.functions[inActionBottom](entry);
            } else {
              // otherwise use the general function
              console.log('enter in general');
              this.functions[inAction](entry);
            }
          } else {
            // if the element is initialy visible
            console.log('enter in general');
            this.functions[inAction](entry);
          }

        } else {
          // element is out of view
          let yPos = entry.boundingClientRect.top;
          let outAction = entry.target.getAttribute('data-iou-out-action');
          let outActionTop = entry.target.getAttribute('data-iou-out-action-top');
          let outActionBottom = entry.target.getAttribute('data-iou-out-action-bottom');

          // here we check if the Element leaves at top or bottom
          if (yPos < 0) {
            // yPos < 0 means the element leaves at top
            if (outActionTop) {
              // if there is a specific top function use it
              this.functions[outActionTop](entry);
            } else if (yPos > 0) {
              // otherwise use the general function
              this.functions[outAction](entry);
            }
          } else {
            // yPos > 0 means the element leaves at bottom
            if (outActionTop) {
              // if there is a specific bottom function use it
              this.functions[outActionBottom](entry);
            } else {
              // otherwise use the general function
              this.functions[outAction](entry);
            }
          }

        }
      });
    });

    elements.forEach(element => {
      observer.observe(element);
    });
  }
}